.business-challenges {
  width: 100%;
  margin-bottom: 6rem;
  padding-top: 2rem;
  background: linear-gradient(to bottom, $blue-50 0%, $white 100%);

  .container {
    width: 100%;
    margin: 0;
    padding: 3rem 1rem;

    @include respond-to(desktop) {
      margin: 0 auto;
      max-width: 1000px;
      flex-direction: row;
    }

    @include respond-to(large-desktop) {
      max-width: 1200px;
    }
  }

  h2 {
    margin-bottom: 4rem;
  }

  .carousel-container {
    position: relative;
  }

  .carousel {
    .carousel-cell {
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 2rem;

      @include respond-to(desktop) {
        flex-direction: row;
      }

      .persona {
        width: 100%;
        display: flex;
        align-items: center;
        flex-direction: column;
        margin-bottom: 2rem;

        @include respond-to(desktop) {
          width: calc(25% - 20px);
          margin: 0;
        }

        .headshot {
          border-radius: 50%;
          width: 175px;
          height: 175px;
          background-size: contain;
          margin-bottom: 2rem;

          img {
            width: 100%;
            height: 100%;
            border-radius: 50%;
          }
        }

        .tagline {
          text-align: center;
          font-weight: fw(bold);
          width: 75%;
        }
      }

      .scenario {
        width: 100%;

        @include respond-to(desktop) {
          width: calc(75% - 20px);
        }
      }

      .title {
        font-weight: fw(bold);
        padding-bottom: 0.5rem;
        border-bottom: 1px solid $grey-500;
      }
    }
  }

  .button {
    display: inline-block;
    padding: 0 0.25rem;
    margin: 0 4px;
    font-size: 0.825rem;
    border: 0;
    border-radius: 6px;
    background-color: transparent;
    line-height: 0.825rem;
    color: $grey-500;

    &:hover {
      cursor: pointer;
    }

    &:focus {
      outline: none;
    }

    &:disabled {
      opacity: 0.3;
    }

    &.is-selected {
      color: $black;
    }

    svg {
      width: 0.5rem;
      height: 0.5rem;

      .arrow {
        fill: color(brand);
      }
    }
  }

  .button-group {
    display: inline-block;
    vertical-align: bottom;

    &:after {
      content: '';
      display: block;
      clear: both;
    }

    .button {
      float: left;
      border-radius: 0;
      border-right-width: 0;
    }
  }

  .button-row {
    text-align: center;

    @include respond-to(desktop) {
      position: absolute;
      top: -10px;
      right: 0;
    }

    .button--previous,
    .button--next {
      font-weight: fw(bold);
      color: color(brand);
    }
  }
}
